<template >
  <div>
    <header>
      <div class="night-day-changebtn">
      </div>
      <div class="user-msg-wrap">
        <div class="user-img-name-wrap">
          <img
            class="user-img"
            src="http://img0.imgtn.bdimg.com/it/u=2925539898,761859990&fm=26&gp=0.jpg"
            alt
          >
          <p class="user-name">{{userTel}}</p>
          <span class="user-chengwei"><span>昵称：</span> {{usernicheng}}</span>
          <span class="user-qianming"><span>签名：</span> {{userqianming}}</span>
        </div>
        <div class="user-working-wrap">
          <p class="p-num">
            <span>0</span>
            <span>{{likedatalength}}</span>
            <span>0</span>
          </p>
          <p class="p-des">
            <span>跟帖</span>
            <span>收藏</span>
            <span>历史</span>
          </p>
        </div>
      </div>
    </header>
    <hr>
    <section id="section">
      <van-cell
        class="uesrlist-msg"
        v-for="item in userList"
        :key="item.id"
        :title="item.title"
        is-link
        :value="item.des"
        @click="changeusermsg(item.title)"
      />
    </section>

    <button class="btn logdown" @click="logindown">注销</button>
  </div>
</template>

<script>
export default {
  name: "mine",
  path: "/mine",
  data() {
    return {
      userList: [
        {
          title: "修改昵称",
          des: "设置昵称"
        },
        {
          title: "修改密码",
          des: "修改密码"
        },
        {
          title: "修改签名",
          des: "设置签名"
        }
      ],
      userTel: "",
      usernicheng: '无',
      userqianming:'无',
      likedatalength:0
    };
  },
  methods: {
    //如果没用登录 直接进入登录页面
    islog() {
      let islog = this.$store.state.islog;
      if (!islog) {
        this.$router.push({ path: "/Logres" });
      }
    },
    getuserMsg() {
      this.userTel = this.$store.state.userTel;
      if (localStorage.getItem('bmob')==null || JSON.parse(localStorage.getItem("bmob")) == '') {
        return
      } else {
      let usermsg = JSON.parse(localStorage.getItem("bmob"));
      this.userTel = usermsg.username
      this.usernicheng = usermsg.nicheng
      this.userqianming = usermsg.qianming
      }

    },
    logindown() {
      let a = confirm("您确定要注销用户吗？");
      if (a) {
        this.$store.commit("changelog", false);
        this.$store.state.userTel = "";
        this.$router.push({ path: "/Logres" });
        localStorage.setItem('bmob',JSON.stringify(''))
      }
    },
    changeusermsg(str){
      this.$router.push({path:'/usermsgchange'})
      this.$store.commit('changeusermsg',str)
    }
  },
  created() {
    this.likedatalength = this.$store.state.likenum
    //异常处理 可能点击路由切换过快 tosat重复加载到另一个路由页面
    this.$toast.clear();
    this.islog();
    this.getuserMsg();
  }
};
</script>

<style lang="less" scopedSlots>
header {
  height: 280px;
  .night-day-changebtn {
    text-align: left;
    height: 60px;
    padding-top: 10px;
    padding-left: 30px;
    button {
      width: 80px;
      height: 30px;
      line-height: 30px;
      border-radius: 10px;
      font-weight: bold;
      font-size: 15px;
      border: 1px solid #ddd;
      background: none;
      cursor: pointer;
    }
  }
  .user-msg-wrap {
    .user-img-name-wrap {
      position: relative;
      height: 100px;
      text-align: left;
      img {
        position: absolute;
        top: 10px;
        left: 30px;
        width: 100px;
        height: 100px;
        border-radius: 50%;
      }
      .user-name {
        position: absolute;
        top: 20px;
        left: 150px;
        display: inline-block;
        font-size: 20px;
        font-weight: bold;
      }
      .user-chengwei {
        position: absolute;
        top: 50px;
        left: 150px;
        font-size: 14px;
        color: rgb(151, 149, 149);
      }
      .user-qianming {
        position: absolute;
        top: 70px;
        left: 150px;
        font-size: 14px;
        color: rgb(151, 149, 149);
      }
    }
    .user-working-wrap {
      margin-top: 40px;
      height: 60px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      p {
        display: flex;
        line-height: 30px;
        justify-content: space-between;
        width: 80%;
        span {
          width: 70px;
        }
      }
      p.p-num {
        font-size: 16px;
        font-weight: bold;
      }
      p.p-des {
        font-size: 14px;
        color: rgb(151, 149, 149);
      }
    }
  }
}
hr {
  background: rgba(218, 216, 216, 0.377);
  height: 5px;
  border: none;
}
section {
  font-size: 18px;
  .uesrlist-msg {
    padding: 10px 10px;
    .van-cell__title {
      text-align: left;
      margin-left: 20px;
      height: 40px;
      line-height: 40px;
      font-weight: lighter;
    }
    .van-cell__value {
      height: 40px;
      line-height: 40px;
    }
    .van-icon {
      height: 40px;
      line-height: 40px;
    }
  }
}
.logdown {
  margin-top: 20px;
  width: 200px;
  height: 40px;
  line-height: 40px;
  border-radius: 10px;
  background: #e41212;
  outline: none;
  border: none;
  color: #fff;
  cursor: pointer;
}
</style>




