
<template>
  <div class="amap-page-container">
    <span class="dingwei" v-if="!city">定位中..</span>
    <div class="show" @click="openbox" v-else>
      <span class="city">{{city}}</span>
      <span class="weather">{{weather}} {{temperature}}°c</span>
    </div>
    <div class="noshow" v-if="isopen">
      <button @click="closebox" class='close'>X</button>
      <p>选择城市<span class='city'>{{chosedcity}}</span></p>
      <ul>
        <li v-for='ct in citylist' :key=ct.id @click="chosecity(ct)">
          <span class='city'>{{ct}}</span>
        </li>
      </ul>
      <button @click="sure" class='sure'>确定</button>
    </div>
  </div>
</template>

  <script>
export default {
  data() {
    return {
      city: "",
      chosedcity:'',
      weather: "",
      temperature: "",
      isopen: false,
      citylist:[
        '成都市',
        '上海市',
        '北京市',
        '眉山市',
      ]
    }
  },
  created() {
    this.wmapinit();
  },
  methods: {
    openbox() {
      this.isopen = true;
      this.chosedcity = this.city
    },
    closebox() {
      this.isopen = false;
    },
    chosecity(ct){
      this.chosedcity = ct
    },
    sure(){
      this.isopen = false;
      this.city = this.chosedcity
      this.getweather()
    },
    wmapinit() {
      if (localStorage.getItem("locmsg") == null) {
        setTimeout(() => {
          this.getcity();
        }, 2000);
        setTimeout(() => {
          this.getweather();
          setTimeout(()=>{
         let locmsg = {
            city: this.city,
            weatherdes: {
              weather: this.weather,
              temperature: this.temperature
            }
          };
          localStorage.setItem("locmsg", JSON.stringify(locmsg));
          },1000)
 
        }, 2300);
      } else {
        let locmsg = JSON.parse(localStorage.getItem("locmsg"));
        this.city = locmsg.city;
        this.weather = locmsg.weatherdes.weather;
        this.temperature = locmsg.weatherdes.temperature;
      }
    },
    getcity() {
      let citySearch = new AMap.CitySearch();
      citySearch.getLocalCity((status, result) => {
        if (status == "complete" && result.info == "OK") {
          // 查询成功，result即为当前所在城市信息
          this.city = result.city;
        }
      });
    },
    getweather() {
      //创建天气查询实例
      let weather = new AMap.Weather();
      //执行实时天气信息查询
      let city = this.city;
      weather.getLive(city, (err, data) => {
        console.log(err, data);
        this.weather = data.weather;
        this.temperature = data.temperature;
      });
    }
  }
};
</script>

<style lang="less" scoped>
.amap-page-container {
  width: 60px;
  height: 50px;
  span {
    display: block;
    height: 25px;
    line-height: 25px;
  }
  .dingwei {
    display: block;
    margin-top: 20px;
    font-weight: bold;
  }
  .show {
    .city {
      font-weight: bold;
    }
  }
  .noshow {
    position: relative;
    width: 200px;
    padding: 10px;
    background: #1571bd;
    p{
      line-height: 20px;
      font-weight: bold;
    }
    .city{
      margin:10px auto;
      width: 60px;
      height: 20px;
      line-height: 20px;
      border-radius: 8px;
      background: #0242a9;
    }
    ul {
        background: #3c81ce;
        li {
          display: inline-block;
          margin:0 5px;
        }
    }
    button{
      background: none;
      border:none;
    }
    .close{
      position: absolute;
      right:10px;
      top:5px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      line-height: 20px;
      color:#fff;
      background: rgb(0, 0, 0);
    }
  }
}
</style>
  
   




