<template >
  <div class="talking-page">
    <header>
      <div class="type-wrap">
        <p
          v-for="item in showlist"
          :key="item.id"
          :class="{'Pchecked':item.type ==type}"
          @click="changeList(item.type)"
        >
          <span>{{ item.title }}</span>
        </p>
      </div>
    </header>
    <!-- 下拉刷新插件 -->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <section class="tuijian-wrap" v-if="type == 'tuijian'">
      <ul>
        <li v-for="(item,index) in duanzidata" :key="item.id">
          <div class="user-msg-box">
            <img class="header" :src="item.header" alt="NOheader">
            <p class="user-name">{{item.username}}</p>
            <button @click="userLike(index)">收藏</button>
          </div>
          <div class="msg-wrap">
            <p class="msg-des">{{item.text}}</p>
            <div class="img-box">
              <img :src="item.image" alt="加载失败">
            </div>
          </div>
          <div class="user-good-box">
            <p>
              <span>赞</span>
              <span>评论...</span>
              <span>分享</span>
            </p>
          </div>
        </li>
      </ul>
    </section>
    <section class="guanzhu-wrap" v-else>
      <div class="guanzhu-men-wrap">
        <p>您暂时没有关注的对象</p>
      </div>
    </section>
</van-pull-refresh>
  </div>
</template>

<script>
export default {
  name: "talking",
  data() {
    return {
      showlist: [
        {
          title: "推荐",
          type: "tuijian"
        },
        {
          title: "关注",
          type: "guanzhu"
        }
      ],
      duanzidata: [],
      Toast: "",
      isok: false,
      type: "",
      isLoading:false
    };
  },
  methods: {
    gettalkingdata() {
      this.Toast = this.$toast({
        type: "loading",
        duration: 0,
        message: "加载中...",
        loadingType: "spinner"
      });
      return  this.axios
        .get("https://www.apiopen.top/satinGodApi?type=1&page=1")
        .then(res => {
          this.isok = true;
          let data = res.data.data.filter(m => m.type == "image");
          this.duanzidata = data;
          return data
        })
        .catch(error => {
          console.log(error);
        });
    },
    changeList(type) {
      this.type = type;
    },
    // 收藏
    userLike(e) {
      let islog = this.$store.state.islog;
      if (!islog) {
        let a = confirm("请您先登录，是否登录？");
        if (a) {
          this.$router.push({ path: "/mine" });
        }
      } else {
        let likebox = []
        let  data = this.duanzidata[e]
        if (localStorage.getItem("userLikeduanzi") == null || JSON.parse(localStorage.getItem('userLikeduanzi') == '')) {
          likebox.push(data)
            this.$toast({
              message:'收藏成功！',
              duration:1000
            })
          localStorage.setItem(
            "userLikeduanzi",
            JSON.stringify(likebox)
          );
            this.$store.commit('addlikedata')
        } else {
          let ishas = false
          let userliked = JSON.parse(localStorage.getItem("userLikeduanzi"));
          likebox = userliked
          console.log(userliked.indexOf(data))
          likebox.forEach(m=>{
            if (m.uid == data.uid) {
              ishas = true
              return
            }
          })
          if (!ishas) {
            likebox.push(data)
            this.$toast({
              message:'收藏成功！',
              duration:1000
            })
            localStorage.setItem('userLikeduanzi',JSON.stringify(likebox))
              this.$store.commit('addlikedata')
          } else {
             this.$toast({
              message:'您已收藏该讯息！',
              duration:1000
            })
          }
        }
      }
    },
    // 刷新数据(该API有的图片没有资源 所以不考虑缓存了 直接刷)
    onRefresh() {
      const data = this.gettalkingdata()
      if (data) {
      setTimeout(() => {
        this.$toast({
          message:'刷新成功',
        duration: 1200,
        });
        this.isLoading = false;
        this.count++;
      }, 500);      
       }
    }
  },
  created() {
    this.type = "tuijian";
    this.gettalkingdata();
  },
  watch: {
    isok() {
      this.Toast.clear();
      this.isok = false;
    }
  }
};
</script>

<style lang="less" scoped=''>
.talking-page {
  background: rgba(221, 221, 221, 0.233);
}
header {
  position: relative;
  height: 40px;
  background: #fff;
  .type-wrap {
    position: absolute;
    display: flex;
    width: 40%;
    justify-content: space-between;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    p {
      position: relative;
      text-align: center;
      font-weight: bold;
      font-size: 14px;
    }
    .Pchecked:after {
      position: absolute;
      top: 0;
      right: -8px;
      content: "";
      display: block;
      width: 5px;
      height: 5px;
      border: 2px solid red;
      border-radius: 50%;
    }
  }
}
.tuijian-wrap {
  font-size: 14px;
  li {
    position: relative;
    padding: 10px;
    margin-top: 6px;
    background: #fff;
    .user-msg-box {
      text-align: left;
      .header {
        display: inline-block;
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      .user-name {
        display: inline-block;
        position: absolute;
        top: 10px;
        left: 70px;
        font-size: 14px;
        font-weight: bold;
      }
      button {
        position: absolute;
        top: 20px;
        right: 10px;
        width: 50px;
        border-radius: 10px;
        background: none;
        cursor: pointer;
        border: 1px solid rgb(190, 186, 186);
        color: rgb(136, 136, 136);
      }
    }
    .msg-wrap {
      margin-top: 10px;
      text-align: left;
      .msg-des {
      }
      .img-box {
        margin-top: 10px;
        width: 200px;
        height: 140px;
        overflow-y: scroll;
        img {
          width: 100%;
        }
      }
    }
    .user-good-box {
      margin-top: 10px;
      width: 100%;
      display: flex;
      justify-content: center;
      p {
        width: 90%;
        display: flex;
        justify-content: space-between;
        span {
          width: 100px;
        }
      }
    }
  }
}
.guanzhu-wrap {
  .guanzhu-men-wrap {
    width: 100%;
    height: 200px;
    p {
      font-size: 16px;
      line-height: 200px;
      font-weight: bold;
      color: rgb(82, 80, 80);
    }
  }
}
.showimg {
  width: 100%;
  height: 100%;
}
</style>



