<template>
  <div id="app">
    <router-view/>
    <Tabber/>
  </div>
</template>

<script>
import Tabber from './components/tabber.vue'

export default {
  name: 'App',
  components:{
    Tabber
  },
  methods:{
    changeactive(path){
      this.$store.commit('changeactive',path)
    }
  },
  created(){
    this.$router.push({path:'/home'})
  },
  watch:{
    $route(to,from){
      console.log(to.path)
      this.changeactive(to.path)
      }
  }
}
</script>

<style>
*{margin:0;padding:0;box-sizing: border-box;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-bottom: 60px;
}
</style>




