<template>
  <div class="msgchange-wrap">
    <header class="header">
      <p>{{nowchangeis}}</p>
    </header>
    <div v-if="nowchangeis == '修改密码'">
      <input v-model="oldpass" type="text" placeholder="请输入旧密码">
      <input v-model="newpass" type="password" placeholder="请输入新密码">
    </div>
    <div v-else-if="nowchangeis == '修改签名'">
      <textarea v-model="userkeymsg" type="text" placeholder="输入您新的签名"></textarea>
    </div>
    <div v-else-if="nowchangeis == '修改昵称'">
      <textarea v-model="userkeymsg" type="text" maxlength="10" placeholder="请输入您新的昵称(最多10个字)"></textarea>
    </div>
    <button v-if="nowchangeis == '修改密码'" @click="changepass">修改密码</button>
    <button v-else @click="changedata">提交</button>
    <button @click="quxiao">取消</button>
  </div>
</template>

<script>
export default {
  name: "usermsgchange",
  data() {
    return {
      nowchangeis: "",
      userkeymsg: "",
      oldpass: "",
      newpass: ""
    };
  },
  methods: {
    getnowchangeis() {
      this.nowchangeis = this.$store.state.nowchangeis;
    },
    changedata(type) {
      //修改bmob云上面的对应数据
      const query = Bmob.Query("_User");
      if (this.nowchangeis == "修改签名") {
        type = "qianming";
      } else if (this.nowchangeis == "修改昵称") {
        type = "nicheng";
      }
      if (this.userkeymsg.length == 0) {
        alert("请输入内容");
      } else {
        let userid = JSON.parse(localStorage.getItem("bmob")).objectId;
        query
          .get(userid)
          .then(res => {
            res.set(type, this.userkeymsg);
            res.save();
            console.log(res);
            this.$toast({
              message: "修改成功！",
              duration: 1000
            });
            setTimeout(() => {
              this.$router.push({ path: "/mine" });
            }, 1000);
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    changepass() {
      let objectId = JSON.parse(localStorage.getItem("bmob")).objectId;
      let data = {
        oldPassword: this.oldpass,
        newPassword: this.newpass
      };
      if (this.oldpass.length == 0 || this.newpass.length == 0) {
        alert("请输入正确格式密码！");
      } else {
        Bmob.updateUserPassword(objectId, data)
          .then(res => {
            console.log(res);
            this.$toast({
              message: "修改成功！",
              duration: 1000
            });
            setTimeout(() => {
              this.$router.push({ path: "/mine" });
            }, 1000);
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    quxiao() {
      history.go(-1);
    }
  },
  created() {
    this.getnowchangeis();
  }
};
</script>

<style lang="less" scoped>
.msgchange-wrap {
  position: relative;
  padding-bottom: 20px;
  header {
    height: 40px;
    line-height: 40px;
    background: red;
    color: #fff;
  }
  .user-msg-now {
    margin-top: 20px;
    margin-bottom: 20px;
    color: red;
  }
  textarea {
    padding-left: 10px;
    padding-top: 5px;
    margin-top: 20px;
    width: 90%;
    line-height: 20px;
    font-size: 14px;
    height: 100px;
    letter-spacing: 1px;
    resize: none;
  }
  input {
    padding-left: 10px;
    font-size: 14px;
    display: inline-block;
    width: 90%;
    margin-top: 10px;
    height: 40px;
  }
  .moren-pass-span {
    display: block;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    background: rgb(41, 38, 38);
    color: #fff;
  }
  button {
    margin: 50px 10px 0;
    width: 100px;
    border: none;
    line-height: 30px;
    color: #fff;
    background: red;
    border-radius: 6px;
    height: 30px;
  }
}
</style>



