<template >
  <div class="talking-page">
    <div class='nologing-wrap' v-if="!islog">
      <p>请您先登录</p>
      <button @click="gotolog">点击登录</button>
    </div>
    <div v-else>
      <van-tabs class="tabs-list" v-model="active" @click="changeList" sticky>
        <van-tab v-for="item in showlist" :key="item.title" :title="item.title">
          <!-- 新闻 -->
          <section class="xinwen-wrap" v-if="type == 'xinwen'">
            <div class='nodata-wrap' v-if="xinwendata.length == 0">
              <p>暂时没有收藏的内容！</p>
            </div>
            <ul v-else>
              <li class="data-list" v-for="(d,index) in xinwendata" :key="d.id">
                <button class="unlike-btn" @click="deletelike(index)">删除</button>
                <div class="hasimg-wrap" v-if="d.picInfo">
                  <p class="data-username">{{d.source}}</p>
                  <p class='data-title'>{{d.title}}</p>
                  <p>{{d.ptime}}</p>
                  <div class="img-wrap">
                    <img :src="pic.url" v-for="pic in d.picInfo" :key="pic.id" alt>
                  </div>
                </div>
              </li>
            </ul>
          </section>
          <!-- 段子 -->
          <section class="duanzi-wrap" v-else>
            <div class='nodata-wrap' v-if="duanzidata.length == 0">
              <p>暂时没有收藏的内容！</p>
            </div>
            <ul v-else>
              <li v-for="(item,index) in duanzidata" :key="item.id">
                <div class="user-msg-box">
                  <img class="header" :src="item.header" alt="NOheader">
                  <p class="user-name">{{item.username}}</p>
                  <button @click="deletelike(index)">删除</button>
                </div>
                <div class="msg-wrap">
                  <p class="msg-des">{{item.text}}</p>
                  <div class="img-box">
                    <img :src="item.image" alt="加载失败">
                  </div>
                  <!-- <div class="showimg img-box">
              <img :src="item.image" alt="加载失败">
                  </div>-->
                </div>
                <div class="user-good-box">
                  <p>
                    <span>赞</span>
                    <span>评论...</span>
                    <span>分享</span>
                  </p>
                </div>
              </li>
            </ul>
          </section>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: "talking",
  data() {
    return {
      active: 0,
      showlist: [
        {
          title: "新闻",
          type: "xinwen"
        },
        {
          title: "段子",
          type: "duanzi"
        }
      ],
      isok: false,
      type: "",
      islog: false,
      duanzidata: [],
      xinwendata: []
    };
  },
  methods: {
    gotolog(){
      this.$router.push({path:'/mine'})
      this.$store.commit('changeactive')
    },
    getduanzidata() {
      if (
        localStorage.getItem("userLikeduanzi") == null ||
        JSON.parse(localStorage.getItem("userLikeduanzi")) == ""
      ) {
        this.duanzidata = [];
      } else {
        this.duanzidata = JSON.parse(localStorage.getItem("userLikeduanzi"));
      }
    },
    getxinwendata() {
      if (
        localStorage.getItem("userLikexinwen") == null ||
        JSON.parse(localStorage.getItem("userLikexinwen")) == ""
      ) {
        this.xinwendata = [];
      } else {
        this.xinwendata = JSON.parse(localStorage.getItem("userLikexinwen"));
      }
    },
    // 改变列表栏
    changeList(index, type) {
      console.log(type);
      if (type == "新闻") {
        this.type = "xinwen";
      } else if ((type = "段子")) {
        this.type = "duanzi";
      }
    },
    // 删除喜欢的讯息
    deletelike(idx){
      let a = confirm('确定删除收藏吗？')
      if (a) {
        if (this.type == 'duanzi') {
        let likebox = JSON.parse(localStorage.getItem('userLikeduanzi'))
        likebox.splice(idx,1)
        this.duanzidata = likebox
        localStorage.setItem('userLikeduanzi',JSON.stringify(likebox))
        } else if (this.type == 'xinwen'){
          let likebox = JSON.parse(localStorage.getItem('userLikexinwen'))
          console.log(likebox[idx])
          likebox.splice(idx,1)
          this.xinwendata = likebox
          localStorage.setItem('userLikexinwen',JSON.stringify(likebox))
          }
        this.$store.commit('dellikedata')
      }
    }
  },
  created() {
      this.$toast.clear();
    this.islog = this.$store.state.islog;
    this.type = "xinwen";
    this.getduanzidata();
    this.getxinwendata();
  }
};
</script>

<style lang="less" scoped>
.talking-page {
  background: rgba(221, 221, 221, 0.233);
  button{
    cursor: pointer;
  }
}
.duanzi-wrap {
  font-size: 14px;
  li {
    position: relative;
    padding: 10px;
    margin-top: 6px;
    background: #fff;
    .user-msg-box {
      text-align: left;
      .header {
        display: inline-block;
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      .user-name {
        display: inline-block;
        position: absolute;
        top: 10px;
        left: 70px;
        font-size: 14px;
        font-weight: bold;
      }
      button {
        position: absolute;
        top: 20px;
        right: 10px;
        width: 50px;
        border-radius: 10px;
        background: none;
        cursor: pointer;
        border: 1px solid rgb(190, 186, 186);
        color: rgb(136, 136, 136);
      }
    }
    .msg-wrap {
      margin-top: 10px;
      text-align: left;
      .msg-des {
      }
      .img-box {
        margin-top: 10px;
        width: 200px;
        height: 140px;
        overflow-y: scroll;
        img {
          width: 100%;
        }
      }
    }
    .user-good-box {
      margin-top: 10px;
      width: 100%;
      display: flex;
      justify-content: center;
      p {
        width: 90%;
        display: flex;
        justify-content: space-between;
        span {
          width: 100px;
        }
      }
    }
  }
  .duanzi-men-wrap {
    width: 100%;
    height: 200px;
    p {
      font-size: 16px;
      line-height: 200px;
      font-weight: bold;
      color: rgb(82, 80, 80);
    }
  }
}
.xinwen-wrap {
  .data-list {
    position: relative;
    overflow: hidden;
    padding:6px 0 6px 10px;
    margin-top: 10px;
    font-size: 12px;
    background: #fff;
    text-align: left;
    .data-username {
      color: red;
    }
    .data-title{
      width: 250px;
    }
    .img-wrap {
      display: flex;
      width: 95%;
      justify-content: space-between;
      img {
        width: 100px;
        height: 60px;
        border-radius: 4px;
      }
    }
    .unlike-btn{
      position: absolute;
      padding:3px 10px;
      width: 50px;
      top:10px;
      right: 10px;
      border-radius: 4px;
      border:none;
      color:#fff;
      background: red;
    }
  }
}
.nodata-wrap{
  font-size: 14px;
  height: 300px;
  line-height: 300px;
}
.showimg {
  width: 100%;
  height: 100%;
}
.nologing-wrap{
  height: 300px;
  background: rgb(255, 255, 255);
  color:#d40707;
  p{
    height: 40px;
    line-height: 40px;
  }
  button{
    width: 100px;
    height: 40px;
    border:none;
    background: #d40707;
    color:#fff;
    border-radius: 5px;
  }
}
</style>



