import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);


const store = new Vuex.Store({
   state:{
    //第一次打开软件判定为没有登录
    islog:false,
    userTel:'',
    userLike:'',
    nowchangeis:'',
    likenum:0,
    active:0,
    showtitle:'',
    mapshow:false,
  },
  mutations:{
    changelog(state,islog){
      //islog = 登录状态
      state.islog = islog
    },
    changeusermsg(state,str){
      state.nowchangeis = str
    },
    getlikenum(state,num){
      state.likenum = num
    },
    addlikedata(state){
      state.likenum++
    },
    dellikedata(state){
      state.likenum--
    },
    changeactive(state,path){
        switch(path){
          case '/home':state.active = 0;break;
          case '/duanzi':state.active = 1;break;
          case '/mylike':state.active = 2;break;
          case '/mine':state.active = 3;break;
      }
    },
    getshowtitle(state,title){
      state.showtitle = title
    }
  }
})

export default store


