<template>
  <div class="tabber-wrap">
    <van-tabbar v-model="active">
      <van-tabbar-item :icon="item.icon" v-for='item in tabberlist' :key = item.id @click="changetab(item.title)">{{ item.title }}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";

export default {
  data() {
    return {
      tabberlist: [
        { title: "新闻" ,icon:'orders-o'},
        { title: "段子" ,icon:"service-o"},
        { title: "收藏" ,icon:"play-circle-o"},
        { title: "我的" ,icon:"user-circle-o"}
      ],
      active: 0
    };
  },
  methods: {
    changetab(title,index){
      switch(title){
        case '新闻':this.$router.push({path:'/home'});break;
        case '段子':this.$router.push({path:'/duanzi'});break;
        case '收藏':this.$router.push({path:'/mylike'});break;
        case '我的':this.$router.push({path:'/mine'});break;
      }
    },
  },
  computed:{
    listenpath(){
      return this.$store.state.active
    }
  },
  watch:{
    listenpath(val){
      console.log(val)
      this.active = val
    }
  }

};
</script>

<style lang="less" scoped>
@import '../../static/css/van.less';
.tabber-wrap{
  position: relative;
  z-index: 100;
}
</style>



