<template>
  <div class="login-res-page">
    <div class="login-res-wrap">
      <p v-if="loging">用户登录</p>
      <p v-else>用户注册</p>
    </div>
    <div class="input-wrap">
      <!-- <van-button
        class="yzma-btn"
        :loading="getingyzm"
        :disabled="getingyzm"
        type="primary"
        @click="getyama"
      >获取验证码</van-button>-->
      <input type="number" maxlength="11" v-model="userTel" placeholder="请输入手机号码">
      <input type="password" maxlength="16" v-model="userYzm" placeholder="请输入密码">
      <button class="gobtn gores-btn" @click="logrescheck" v-if="loging">新用户？点击注册</button>
      <button class="gobtn gologin-btn" @click="logrescheck" v-else>返回登录</button>
    </div>
    <div class="btn-wrap">
      <button class="btn login-res-btn" v-if="loging" @click="login">登录</button>
      <button class="btn login-res-btn" v-else @click="register">注册</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userTel: "",
      userYzm: "",
      loging: true,
      getingyzm: false
    };
  },
  methods: {
    logrescheck() {
      this.loging = !this.loging;
      this.userTel = "";
      this.userYzm = "";
    },
    // 登录
    login() {
      if (this.userTel.length != 11 || this.userYzm.length == 0) {
        this.$toast({
          message: "请输入正确格式信息！",
             duration:1000
        });
      } else {
        Bmob.User.login(this.userTel, this.userYzm)
          .then(res => {
            this.$store.state.userTel = this.userTel;
            this.$store.commit("changelog", true);
            this.$toast({
              message: "登录成功! 正在跳转..."
            });
            setTimeout(() => {
              this.$router.push({ path: "/mine" });
            }, 1000);
          })
          .catch(err => {
            console.log(err);
            if (err.code == 101) {
              this.$toast({
                message: "用户未注册/手机号码或密码错误，请输入正确信息!",
                   duration:1000
              });
            }
          });
      }
    },
    // 注册
    register() {
      if (this.userTel.length != 11 ||this.userYzm.length == 0) {
        this.$toast({
          message: "请输入正确格式信息！",
             duration:1000
        });
      } else {
        let params = {
          username: this.userTel,
          password: this.userYzm,
          nicheng: "无",
          qianming: "这个人很懒，什么都没留下"
        };
        Bmob.User.register(params)
          .then(res => {
            console.log(res);
            this.$store.state.userTel = this.userTel;
            this.$store.commit("changelog", true);
            this.$toast({
              message: "注册成功！ 正在登录...",
              duration:1000
            });
            setTimeout(()=>{
              this.login()
            },1000)
          })
          .catch(err => {
            console.log(err);
            if (err.code == 202) {
              alert("当前用户已经注册！");
            }
          });
      }
    },
    // 获取验证码
    getyama() {
      //获取随机数 且 若将验证码转为float 数字 会有不同位数数字出现
      if (this.userTel.length == 0) {
        this.$toast({
          message: "请输入正确的手机号码！",
          duration: 1000
        });
      } else {
        this.getingyzm = true;
        let num = Math.random()
          .toString()
          .slice(5, 11);
        this.yzma = num;
        setTimeout(() => {
          alert("您的验证码为" + num);
          this.getingyzm = false;
        }, 3000);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.login-res-page {
  font-size: 14px;
  button {
    border: none;
    cursor: pointer;
  }
  .login-res-wrap {
    height: 40px;
    line-height: 40px;
    background: rgb(212, 7, 7);
    p {
      color: #fff;
      font-size: 16px;
      font-weight: bold;
    }
  }
  .input-wrap {
    position: relative;
    margin: 50px auto 0;
    width: 100%;
    height: 200px;
    input {
      display: inline-block;
      padding-left: 10px;
      margin-top: 10px;
      width: 80%;
      height: 50px;
      letter-spacing: 1px;
      border: none;
      border-bottom: 1px solid #ddd;
    }
    .yzma-btn {
      position: absolute;
      line-height: 30px;
      height: 30px;
      width: 100px;
      top: 20px;
      right: 50px;
      border-radius: 10px;
      font-size: 12px;
      background: rgb(212, 7, 7);
      color: #fff;
    }
    .gobtn {
      display: block;
      margin-top: 10px;
      margin-left: 50px;
      border-radius: 0;
      background: none;
      color: red;
    }
  }
  .btn-wrap {
    .btn {
      height: 40px;
      padding: 10px;
      border-radius: 10px;
    }
    .login-res-btn {
      width: 120px;
      color: #fff;
      background: rgb(212, 7, 7);
    }
  }
}
</style>



