import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/tabber/home/home.vue'
import Mylike from '@/components/tabber/mylike/mylike.vue'
import Duanzi from '@/components/tabber/duanzi/duanzi.vue'
import Mine from '@/components/tabber/mine/mine.vue'
import Logres from '@/components/tabber/mine/resAndlog.vue'
import Usermsgchange from '@/components/tabber/mine/minemsgchange.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/mylike',
      name: 'mylike',
      component: Mylike
    }, 
    {
      path: '/duanzi',
      name: 'duanzi',
      component: Duanzi
    }, 
    {
      path: '/mine',
      name: 'mine',
      component: Mine,
    },
    {
      path: '/logres',
      name: 'logres',
      component: Logres,
    },
    {
      path:'/usermsgchange',
      name:'usermsgchange',
      component:Usermsgchange
    }
  ]
})



