<template >
  <div class="home-page">
    <header>
      <div class="app-icon" @click="getbackdata">坤</div>
      <div class="chajian" @click="setcity">
        <Map class="mapbox"/>
      </div>
      <van-search
        class="search-wrap"
        v-model="value"
        placeholder="请输入搜索关键词"
        show-action
        @search="onSearch"
        background="none"
      >
        <div slot="action" @click="onSearch" style="color:#fff">搜索</div>
      </van-search>
    </header>
    <div class="vanbox">
      <van-tabs class="tabs-list" v-model="active" @click="changedata" sticky>
        <van-tab v-for="item in tablist" :key="item.title" :title="item.title">
          <div v-if="datalength == 0">
            <p>暂时没有您搜索的数据!</p>
          </div>
          <!-- 展示新闻数据 -->
          <ul v-if="showtitle != 'video'">
            <li class="data-list" v-for="(d,index) in newsdata" :key="d.id">
              <button class="like-btn" @click="userLike(index)">收藏</button>
              <div class="hasimg-wrap" v-if="d.picInfo">
                <p class="data-username">{{d.source}}</p>
                <p class="data-title">{{d.title}}</p>
                <p>{{d.ptime}}</p>
                <div class="img-wrap">
                  <img :src="pic.url" v-for="pic in d.picInfo" :key="pic.id" alt>
                </div>
              </div>
            </li>
          </ul>
          <!-- 视频元素 展示视频数据 -->
          <ul class="video-list" v-else-if="showtitle == 'video'">
            <li v-for="(v,index) in videodata" :key="index">
              <p class="video-type">{{v.data.content.data.category}}</p>
              <p class="video-author">{{v.data.content.data.author.description}}</p>
              <video :src="v.data.content.data.playUrl" controls></video>
              <p class="video-des">{{ v.data.content.data.description}}</p>
            </li>
          </ul>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import Map from "./map.vue";
import { setTimeout } from "timers";

export default {
  name: "home",
  path: "/home",
  components: {
    Map
  },
  data() {
    return {
      alldata: [],
      value: "",
      active: 0,
      datalength: 1,
      newsdata: [],//新闻数据
      videodata: [],//视频数据
      showtitle: "",//展示标题
      type: "dy",//第一个展示数据类型
      isok: false,
      Toast: "",
      nowtitle: "",
      tablist: [
        { title: "关注", type: "dy" },
        { title: "头条", type: "toutiao" },
        { title: "视频", type: "video" },
        { title: "娱乐", type: "ent" },
        { title: "体育", type: "sports" }
      ]
    };
  },
  methods: {
    //进入首页检测是否登录
    islogin() {
      if (localStorage.getItem('bmob')&&JSON.parse(localStorage.getItem('bmob')).length != 0) {
        this.$store.state.islog = true;
      } else {
        this.$store.state.islog = false;
      }
    },
    // 重新定位
    setcity() {
      this.$store.state.mapshow = true;
    },
    // 搜索功能
    onSearch() {
      console.log(this.nowtitle);
      let searchbox = [];
      let data = [];
      if (this.value.length == 0) {
        this.newsdata = this.alldata;
        this.videodata = this.alldata;
        this.datalength = this.alldata.length;
      } else {
        //视频栏目单独处理
        if (this.nowtitle == "视频") {
          data = this.videodata;
          data.forEach(v => {
            let reg = v.data.content.data.author.description;
            let des = v.data.content.data.description;
            let category = v.data.content.data.category;
            if (
              reg.indexOf(this.value) != -1 ||
              des.indexOf(this.value) != -1 ||
              category.indexOf(this.value) != -1
            ) {
              searchbox.push(v);
            }
          });
          this.videodata = searchbox;
          this.datalength = this.videodata.length;
        }
        //非视频栏目
        else if (this.nowtitle != "视频") {
          data = this.alldata;
          data.forEach(m => {
            //模糊搜索 超简单search查询功能
            let reg = m.title;
            let source = m.source;
            // 可能查询到的媒体名字或者标题内容名（内容信息也一样道理）
            if (
              reg.indexOf(this.value) != -1 ||
              source.indexOf(this.value) != -1
            ) {
              searchbox.push(m);
            }
          });
          this.newsdata = searchbox;
          this.datalength = this.newsdata.length;
        }
      }
    },
    //重置
    getbackdata() {
      this.value = "";
      this.datalength = 1;
      if (this.nowtitle == "视频") {
        this.getvideo();
      } else {
        this.getdata(this.nowtype);
      }
    },
    getdata(type) {
      if (localStorage.getItem("datalist" + type)) {
        this.newsdata = JSON.parse(localStorage.getItem("datalist" + type));
        this.alldata = [...this.newsdata];
      } else {
        this.Toast = this.$toast({
          type: "loading",
          duration: 0,
          message: "加载中...",
          loadingType: "spinner"
        });
        this.newsdata = [];
        this.axios
          .get("https://www.apiopen.top/journalismApi")
          .then(response => {
            this.isok = true;
            if (type == "ent") {
              let box = response.data.data[type];
              box.splice(3, 1);
              this.newsdata = box;
            } else {
              this.newsdata = response.data.data[type];
            }
            this.alldata = [...this.newsdata];
            localStorage.setItem(
              "datalist" + type,
              JSON.stringify(this.newsdata)
            );
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    getvideo() {
      if (localStorage.getItem("datalist-video")) {
        this.videodata = JSON.parse(localStorage.getItem("datalist-video"));
            this.alldata = [...this.videodata];
      } else {
        this.Toast = this.$toast({
          type: "loading",
          duration: 0,
          message: "加载中...",
          loadingType: "spinner"
        });
      this.axios
        .get("https://api.apiopen.top/todayVideo")
        .then(res => {
          // this.newsdata = res.data.data
          this.isok = true;
          let box = [];
          box = res.data.result;
          //过滤
          box = box.filter(m => m.type == "followCard");
          // 有时候过滤比遍历好用
          // box.forEach((m,index)=>{
          //   // if (m.type == "pictureFollowCard" || m.type == "textCard"  ) {
          //   //   box.splice(box.indexOf(m),1)
          //   // }
          //   m.filter()
          // })
          this.videodata = box;
          this.alldata = [...this.videodata];
          localStorage.setItem("datalist-video", JSON.stringify(this.videodata));
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    //改变展示内容
    changedata(index, title) {
      let type = "";
      if (title == this.nowtitle) {
        return;
      } else {
        this.datalength = 1;
        // 由于API中的key不一样 所以视频单独给定一个函数以及标签设置其对应内容
        switch (title) {
          case "关注":
            (this.showtitle = ""),
              (type = "dy"),
              this.getdata(type),
              (this.nowtitle = title),
              (this.nowtype = type);
            break;
          case "头条":
            (this.showtitle = ""),
              (type = "toutiao"),
              this.getdata(type),
              (this.nowtype = type);
            this.nowtitle = title;
            break;
          case "视频":
            (this.showtitle = "video"),
              (type = ""),
              this.getvideo(),
              (this.nowtype = type);
            this.nowtitle = title;
            break;
          case "娱乐":
            (this.showtitle = ""),
              (type = "ent"),
              this.getdata(type),
              (this.nowtype = type);
            this.nowtitle = title;
            break;
          case "体育":
            (this.showtitle = ""),
              (type = "sports"),
              this.getdata(type),
              (this.nowtype = type);
            this.nowtitle = title;
            break;
        }
      }
    },
    //获取用户收藏信息
    getlikedata() {
      let xinwennum = 0;
      let duanzinum = 0;
      if (
        localStorage.getItem("userLikexinwen") == null ||
        JSON.parse(localStorage.getItem("userLikexinwen")) == ""
      ) {
        xinwennum = 0;
      } else {
        xinwennum = JSON.parse(localStorage.getItem("userLikexinwen")).length;
      }
      if (
        localStorage.getItem("userLikeduanzi") == null ||
        JSON.parse(localStorage.getItem("userLikeduanzi")) == ""
      ) {
        duanzinum = 0;
      } else {
        duanzinum = JSON.parse(localStorage.getItem("userLikeduanzi")).length;
      }
      let num = xinwennum + duanzinum;
      this.$store.commit("getlikenum", num);
    },
    // 收藏新闻
    userLike(e) {
      let islog = this.$store.state.islog;
      if (!islog) {
        let a = confirm("请您先登录，是否登录？");
        if (a) {
          this.$router.push({ path: "/mine" });
        }
      } else {
        let likebox = [];
        let data = this.newsdata[e];
        if (
          localStorage.getItem("userLikexinwen") == null ||
          JSON.parse(localStorage.getItem("userLikexinwen") == "")
        ) {
          likebox.push(data);
          this.$toast({
            message: "收藏成功！",
            duration: 1000
          });
          localStorage.setItem("userLikexinwen", JSON.stringify(likebox));
          this.$store.commit("addlikedata");
        } else {
          let ishas = false;
          let userliked = JSON.parse(localStorage.getItem("userLikexinwen"));
          likebox = userliked;
          likebox.forEach(m => {
            if (m.title == data.title) {
              ishas = true;
              return;
            }
          });
          if (!ishas) {
            likebox.push(data);
            this.$toast({
              message: "收藏成功！",
              duration: 1000
            });
            localStorage.setItem("userLikexinwen", JSON.stringify(likebox));
            this.$store.commit("addlikedata");
          } else {
            this.$toast({
              message: "您已收藏该讯息！",
              duration: 1000
            });
          }
        }
      }
    },
    //获取本地缓存数据（非首次打开或未清除本地缓存）
  },
  created() {
    this.islogin()
    this.getdata(this.type);
    this.changedata(1, "关注");
    this.getlikedata();
  },
  watch: {
    isok() {
      this.Toast.clear();
      this.isok = false;
    }
  }
};
</script>
<style lang='less'>
@import '../../../../static/css/van.less';
</style>

<style  lang="less" scoped>
.home-page {
  background: rgba(221, 221, 221, 0.233);
  box-sizing: border-box;
  position: relative;
  header {
    position: relative;
    height: 70px;
    background: #d40707;
    line-height: 70px;
    .app-icon {
      position: absolute;
      font-size: 30px;
      color: #fff;
      font-weight: bold;
      left: 30px;
      top: 50%;
      transform: translateY(-50%);
    }
    .chajian {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 70px;
      font-size: 12px;
      color: #fff;
      p {
        height: 20px;
        line-height: 20px;
      }
      z-index: 1000;
    }
    .search-wrap {
      position: absolute;
      width: 250px;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      .van-field__control {
        background: rgb(56, 30, 30) !important;
      }
    }
  }
  .data-list {
    position: relative;
    overflow: hidden;
    padding: 6px 0 6px 10px;
    margin-top: 10px;
    font-size: 12px;
    background: #fff;
    text-align: left;
    .data-username {
      color: red;
    }
    .data-title {
      width: 250px;
    }
    .img-wrap {
      display: flex;
      width: 95%;
      justify-content: space-between;
      img {
        width: 100px;
        height: 60px;
        border-radius: 4px;
      }
    }
    .like-btn {
      position: absolute;
      padding: 3px 10px;
      width: 50px;
      top: 10px;
      right: 10px;
      border-radius: 4px;
      border: none;
      color: #fff;
      background: red;
    }
  }
  .video-list {
    li {
      position: relative;
      padding: 10px;
      p {
        margin: 0 auto;
        width: 80%;
        text-align: left;
        font-size: 14px;
      }
      .video-type {
        position: absolute;
        width: 61px;
        text-align: center;
        height: 20px;
        top: 10px;
        line-height: 20px;
        left: 20px;
        font-size: 14px;
        color: #ffffff;
        border-radius: 5px;
        background: #b1abac;
      }
      .video-author {
        position: absolute;
        top: 38px;
        left: 22px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 80%;
      }
      .video-des {
        background: #ddd;
        border-radius: 5px;
        padding: 5px;
      }
      video {
        width: 90%;
        height: 400px;
      }
    }
  }
}

</style>



